.not-clickable {
  -webkit-user-select: none;  /* Chrome all / Safari all */
  -moz-user-select: none;     /* Firefox all */
  -ms-user-select: none;      /* IE 10+ */
  user-select: none; 
  cursor: default;
}

.snakes-and-ladders-page {
  min-height: 100vh;
  font-family: Merienda;
  background-image: url('https://st2.depositphotos.com/2485347/8466/v/950/depositphotos_84668354-stock-illustration-background-of-cartoon-dices.jpg');
}

.page-shadow {
  min-height: 100vh;
  background: 
    linear-gradient(
      90deg, 
      rgba(11, 0, 131, 0.95), 
      rgba(58, 0, 0, 0.96), 
    )
  ;
}

.box-cover-color{
  background-color: rgba(0, 0, 0, 0.5);
}

.board-cover-color{
  background-color: rgba(0, 0, 0, 0.2);
}

.box-shadow {
  box-shadow: 0px 0px 60px rgb(155, 155, 155);
  @extend .box-cover-color
}

.form-control{
  background-color: rgba(0, 0, 0, 0.1);

  &:focus {
    background-color: rgba(0, 0, 0, 0.1);
    box-shadow: inset 0 0px 10px rgba(0, 0, 0, 0.075), 0 0 8px rgba(255, 255, 255, 0.6);
  }
}
.snakes-and-ladders-box {
    display: grid;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: repeat(10, 1fr);
    gap: 0px 0px;
    grid-auto-flow: row;
    grid-template-areas:
      ". . . . . . . . . ."
      ". . . . . . . . . ."
      ". . . . . . . . . ."
      ". . . . . . . . . ."
      ". . . . . . . . . ."
      ". . . . . . . . . ."
      ". . . . . . . . . ."
      ". . . . . . . . . ."
      ". . . . . . . . . ."
      ". . . . . . . . . .";
      
      height: 65vh;
      width: 65vh;
      padding: 0px;
      align-items: center;
      color: rgba(204, 204, 215, 0);
}

.snakes-and-ladders-board {
  background-image: url("../../assets/game-board.png");
  background-repeat: no-repeat;
  background-size: 65vh 65vh;
}

