.cursor-pointer {
      cursor: pointer;
}

.hover-scale {
      transition: transform 0.25s ease-out;   

      &:hover {
      transform: scale(1.05);
      }
}

.hover-scale-change {
      transition: transform 0.25s ease-out;
   
      &:hover {
      transform: scale(1.20);
      }

      :active {
      color: rgb(255, 255, 255);
      }
}

.hover-scale-rotate {
      transition: transform 0.25s ease-out;
      
      &:hover {
      transform: rotate(120deg);
      }

      &:active {
      transform: rotate(270deg);
      }
}

.box-animation {

      &:active {
            animation: boxAnimation 1s linear 0s 1 normal none;
      }
}

@keyframes boxAnimation {
      0% {
        opacity: 0.8;
      }
    
      100% {
        opacity: 1;
      }
}

.auth-img-library-animation {

      filter: grayscale(90%);

      &:hover {
            filter: grayscale(0%);
      }

      &:active {
            filter: grayscale(50%)
      }
}


.auth-name-library-animation {

      filter: grayscale(50%);

      &:hover {
            filter: grayscale(0%);
      }
}
